body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background: rgb(35, 35, 40);
    color: rgb(46, 154, 208);
}

.spinner {
    width:50px;
    height:50px;
    border-radius:32px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    border:2px groove white;
    position:absolute;
    left:50%;
    top:150%;
    margin:-16px 0px 0px -16px;
    border-top-color:rgba(46,154,208,1);
    border-left-color:rgba(46,154,208,0.9);
    border-bottom-color:rgba(46,154,208,0.8);
    border-right-color:rgba(46,154,208,0.7);
    -webkit-animation: rotate 1000ms infinite linear;
            animation: rotate 1000ms infinite linear;
    -webkit-transform:translateZ(0px);
            transform:translateZ(0px);
}


@-webkit-keyframes rotate {
    0% {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    100% {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}


@keyframes rotate {
    0% {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    100% {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}


